import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { DragScrollComponent } from 'ngx-drag-scroll';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent {
  @ViewChild('loginInput') myDiv: ElementRef;
  @ViewChild('someInput') someInput: ElementRef;

  sp = -118;
  currentCard =6;
  maxCard = 7;
  minCard = 0;
  images=["../../../assets/img.jpg","../../../assets/img.jpg","../../../assets/img.jpg","../../../assets/img.jpg","../../../assets/img.jpg","../../../assets/img.jpg","../../../assets/img.jpg","../../../assets/img.jpg"];
  images2=["../../../assets/imag.jpg","../../../assets/imag.jpg","../../../assets/imag.jpg","../../../assets/imag.jpg","../../../assets/imag.jpg"];
  items: Array<any> = []

  constructor() {
    
    this.items = [
    { name: 'assets/images/thumb1.png' },
    { name: 'assets/images/thumb2.png' },
    { name: 'assets/images/thumb3.png' },
    { name: 'assets/images/thumb4.png' },
    { name: 'assets/images/thumb5.png' },
    { name: 'assets/images/thumb6.png' },
    { name: 'assets/images/thumb1.png' },
    { name: 'assets/images/thumb2.png' },
    { name: 'assets/images/thumb3.png' },
    { name: 'assets/images/thumb4.png' },
    { name: 'assets/images/thumb5.png' },
    { name: 'assets/images/thumb6.png' },
  ]}

  next(event){
    if (this.currentCard <= this.minCard){
      return
    }
    this.someInput.nativeElement.classList.add('next');
    this.sp += 27;
    this.myDiv.nativeElement.parentElement.childNodes[this.currentCard].classList.remove('custom-card');
    this.currentCard -= 1;
    this.myDiv.nativeElement.parentElement.childNodes[this.currentCard].classList.add('custom-card');
    this.someInput.nativeElement.style.right= this.sp+'rem';
  }
  before(event){
    if (this.currentCard >= this.maxCard){
      return
    }
    this.someInput.nativeElement.classList.add('before');
    this.sp -= 27;    
    this.myDiv.nativeElement.parentElement.childNodes[this.currentCard].classList.remove('custom-card');
    this.currentCard += 1;
    this.myDiv.nativeElement.parentElement.childNodes[this.currentCard].classList.add('custom-card');
    this.someInput.nativeElement.style.right= this.sp+'rem';
  }
  ngAfterViewInit(){
    this.myDiv.nativeElement.parentElement.childNodes[6].classList.add('custom-card');
  }
}
