import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-news-internal',
  templateUrl: './news-internal.component.html',
  styleUrls: ['./news-internal.component.scss']
})
export class NewsInternalComponent implements OnInit {
  id = 0;
  newsInfo=[
    {
      name: 'خبر 1',
      id: 1
    },
    {
      name: 'خبر 2',
      id: 2
    },
    {
      name: 'خبر 3',
      id: 3
    },
    {
      name: 'خبر 4',
      id: 4
    },
    {
      name: 'خبر 5',
      id: 5
    },
    {
      name: 'خبر 6',
      id: 6
    },
    {
      name: 'خبر 7',
      id: 7
    },
  ];
  newInfo=this.newsInfo[this.id];
  constructor(private route:ActivatedRoute) {}
  ngOnInit(): void {
    this.route.paramMap.subscribe(params => {
      this.id = +params.get('id');
      this.newInfo=this.newsInfo.find(x => x.id == this.id);
    });
  }

}
