import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { NewsArchieveComponent } from './news-archieve/news-archieve.component';
import { NewsInternalComponent } from './news-internal/news-internal.component';

const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'newsInternal', component: NewsInternalComponent},
  {path: 'newsInternal/:id', component: NewsInternalComponent},
  {path: 'newsArchieve', component: NewsArchieveComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    scrollPositionRestoration: 'enabled'
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
